                      class="btn btn-default"
                      style="border-radius: 9px; background:#3460A9; color:white"
                      @click="Save4()"
                    >Save</button>
                  </div>
                  <div class="btn-group" role="group">
                    <button
                      type="button"
                      class="btn btn-default"
                      style="border-radius: 9px;color:#3460A9"
                      @click="Cancel4()"
                    >Cancel</button>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <!-- --------------------------- -->
        </el-tab-pane>
        <!-- -------------------------------------------4.E-Mail页面------------------------------------------ -->
        <el-tab-pane label="Notification" name="fourth">
          <div class="mail-1" style="height:26em">
            <p
              style="padding-top: 5px; line-height: 30px;font-size: 18px;font-weight: bold;"
            >E-Mail Configuration</p>
            <div class="div-1">SMTP Server</div>
            <input :disabled="disabled" :style="fontstyle" v-model="email_list.server" />
            <div class="div-7" v-show="showEdit" @click="E_Edit()">Edit</div>
            <div class="div-7" v-show="showCancel" @click="E_Cancel()">Cancel</div>
            <div class="div-1">Sever Port</div>
            <input :disabled="disabled" :style="fontstyle" v-model="email_list.port" />
            <div class="div-1">Encrypt Method</div>
            <input :disabled="disabled" :style="fontstyle" v-model="email_list.method" />
            <div class="div-1">Account</div>
