            >Time Set Configuration</p>
            <div class="div-1">Date</div>
            <input :disabled="disabled" :style="fontstyle" v-model="email_list.port" />
            <div class="div-7">Edit</div>
            <div class="div-1">time</div>
            <input :disabled="disabled" :style="fontstyle" v-model="email_list.port" />
            <div class="div-1">Time zone</div>
            <input :disabled="disabled" :style="fontstyle" v-model="email_list.port" />
            <div class="div-1">Auto configuration</div>
            <input :disabled="disabled" :style="fontstyle" v-model="email_list.port" />
            <div class="div-8">Save</div>
            <div class="div-9">Send</div>
          </div>
        </el-tab-pane>-->
        <!-- -------------------------------------------6.System Backup页面-------------------------------------->
        <el-tab-pane label="System Backup" name="sixth">
          <div class="backup-1" style="height:26em">
            <p
              style="padding-top: 5px; line-height: 30px;font-size: 18px;font-weight: bold;"
            >System Backup Configuration</p>
            <div class="div-1">System Backup</div>
            <div class="div-2">
              <p>
                <input id="box1" type="checkbox" name="item" />
                <label>System configuration Data</label>
              </p>
            </div>
            <div class="div-3">
              <button @click="Backup()">Start</button>
            </div>
            <div class="div-1">System Upgrade</div>
            <div class="div-2" style="line-height: 0;">
              <input
                ref="inputUrl"
                type="file"
                name="item"
                value
                placeholder="Select the upgrade file …"
                style="margin: 0.5rem 0 1.5rem 0.5rem;border: 0.01rem solid #000;padding: 0.2rem;width: 50rem;"
              />
              <div>
                <span style="margin: 0.5rem;">
                  <strong>Note:</strong> the original system configuration will not be retained after the upgrade!
                </span>
              </div>
            </div>
            <div class="div-3">
              <button @click="Upgrade()">Start</button>
            </div>
          </div>
        </el-tab-pane>
        <!-- -------------------------------------------7.Operating Date页面------------------------------------->
        <el-tab-pane label="Operating Data" name="seventh">
          <div class="operating-1" style="height:26em">
            <div class="select-3">
              <div class="select-3-1">
                <el-select v-model="username" placeholder="Operator">
                  <el-option
                    v-for="item in user_list"
                    :key="item.id"
                    :label="item.account"
                    :value="item.account"
                  ></el-option>
                </el-select>
                <el-date-picker
                  style="padding-top: 4px;"
                  v-model="value1"
                  type="daterange"
                  range-separator="To"
                  start-placeholder="Start Time"
                  end-placeholder="End Time"
                ></el-date-picker>
                <button @click="Search()">Search</button>
              </div>
              <!-- 表格展示 -->
              <el-table :data="user_status_list" style="width: 100%">
                <el-table-column prop="username" label="Operator" width="180"></el-table-column>
                <el-table-column prop="grader" label="Grade" width="180"></el-table-column>
                <el-table-column prop="logintime" label="Login Time"></el-table-column>
                <el-table-column prop="outtime" label="Out Time"></el-table-column>
              </el-table>
            </div>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
    <!-- 三、底部信息栏 -->
    <globar></globar>
  </div>
</template>
<script>
import {
  All_site,
  newAddress_user,
  newAddress,
  onDelete_address,
  onModify_address_put,
  newUser,
  onDelete_user,
  onModify_user_put,
  cameras,
  cameras_post,
  cameras_delete,
  cameras_update,
  email_get,
  email_update,
  email_test,
  sql_post,
  sql_put,
  userstatus_post,
  PasswordVerily
} from "../Api/api";
export default {
  inject: ["reload"], // 局部刷新
  data() {
    return {
      //
      value: "",
      // 默认选中第一个页面
      activeName: "first",
      // site弹窗的数据绑定
      form: {},
      // account弹窗的数据绑定
      formAccount: {},
      // Cameras摄像头数据绑定
      formCameras: {},
      // 禁止input框输入 点击修改 改变颜色 展示密码 展示取消按钮
      disabled: true,
      fontstyle: { color: "" },
      password: "password",
      showEdit: true,
      showCancel: false,
      // 1、获取当前用户管理的所有设备的信息
      sites_list: [],
      // 2、获取所有的用户信息
      user_list: [],
      // 3、获取摄像头信息
      cameras_list: [],
      // 4、获取邮件配置信息
      email_list: [],
      test: "",
      // 7、 查询用户记录
      username: "",
      user_status_list: [],
      // 日期选择器的值
      value1: "",
      // 是否点击了搜索按钮
      search_flg: false
    };
  },
  created() {
    // 0、局部刷新 后保留在当前页面
    if (sessionStorage.getItem("activeName")) {
      this.activeName = sessionStorage.getItem("activeName");
    }
    // 1、获取当前用户管理的所有设备的信息
    this.get_site();
    // 2、获取所有的用户信息
    this.get_user();
    // 3、获取摄像头信息
    this.get_cameras();
    // 4、获取邮件配置信息
    this.get_email();
  },
  methods: {
    // ---------------------------------------1.Sites页面--------------------------------------------
    // 1、获取当前用户管理的所有设备的信息
    get_site() {
      let params = {
        grade: localStorage.getItem("grade"),
        user: localStorage.getItem("user")
      };
      All_site(params).then(e => {
        this.sites_list = e.data;
        // console.log("sites_list:", this.sites_list);
      });
    },
    // 2、获取所有的用户信息
    get_user() {
      newAddress_user().then(e => {
        this.user_list = e.data;
        // console.log("user_list:", this.user_list);
      });
    },
    // 3、获取摄像头信息
    get_cameras() {
      cameras().then(e => {
        this.cameras_list = e.data;
        // console.log("cameras_list:", this.cameras_list);
      });
    },
    // 4、获取邮件配置信息
    get_email() {
      email_get().then(e => {
        this.email_list = e.data;
        // console.log(this.email_list)
      });
    },
    // ---修改设备信息 中的edit save delete cancel按钮---
    // 点击修改按钮将对应的数据填充上去
    Edit(item) {
      let params = {
        username: localStorage.getItem("user"),
        password: prompt("Please enter your password")
      };
      PasswordVerily(params).then(e => {
        if (e.data != "ok") {
          alert(e.data);
        } else {
          $("#myModal-1").modal("show");
          this.form = item;
        }
      });
    },
    Delete1() {
      sessionStorage.setItem("activeName", this.activeName);
      // 点击Edit按钮已经将本条对应数据填充上去了
      let params = { ip: this.form.ip };
      onDelete_address(params).then(e => {
        alert(e.data);
        this.reload();
      });
      $("#myModal-1").modal("hide");
    },
    Save1() {
      if (this.search_flg == false) {
        alert("Please click the red search location icon to locate");
      } else {
        this.search_flg = false;
        this.$refs.gmap2.Save();
        sessionStorage.setItem("activeName", this.activeName);
        $("#myModal-1").modal("hide");
        let params = {
          site: this.form.site,
          location: this.form.location,
          ip: this.form.ip,
          communication: this.form.communication,
          type: this.form.type,
          serial: this.form.serial,
          addUser: this.form.addUser
        };
        // console.log("111111111111111", params);
        if (!params.site) {
          alert("Site not entered");
        } else {
          onModify_address_put(params).then(e => {
            alert(e.data);
            // 局部刷新必须放在返回结果后面 不然会异步执行
            this.reload();
          });
        }
      }
    },
    Cancel1() {
      $("#myModal-1").modal("hide");
    },
    // ---添加设备信息 中的save delete cancel按钮---
    Clear() {
      this.form = {};
    },
    Save() {
      if (
        this.form.site == "" ||
        this.form.location == "" ||
        this.form.ip == "" ||
        this.form.communication == "" ||
        this.form.type == "" ||
        this.form.serial == "" ||
        this.form.addUser == ""
      ) {
        alert("You have unfilled data");
      } else {
        // 调用地图组件的搜索方法( 同时调用会先执行Save()方法）
        // this.$refs.gmap.Search();
        // 调用地图组件的Save方法保存坐标点
        if (this.search_flg == false) {
          alert("Please click the red search location icon to locate");
        } else {
          this.search_flg = false;
          this.$refs.gmap.Save();
          // this.sschildren()
          // 将页面切换的当前索引存入sessionstorage，作用是再局部刷新时，保持再当前页面
          sessionStorage.setItem("activeName", this.activeName);
          // ps这里新增加了站点 需要将ip加入到IP池，不然刷新地图不会有坐标点显示（先获取在添加）
          let ip_list = JSON.parse(localStorage.getItem("ip"));
          ip_list.push(this.form.ip); // push是有返回值的
          localStorage.setItem("ip", JSON.stringify(ip_list)); // localStorage只接收字符串类型
          // 关闭遮罩层
          $("#myModal").modal("hide");
          let params = {
            site: this.form.site,
            location: this.form.location,
            ip: this.form.ip,
            communication: this.form.communication,
            type: this.form.type,
            serial: this.form.serial,
            addUser: this.form.addUser
          };
          newAddress(params).then(e => {
            alert(e.data);
            this.reload();
          });
        }
      }
    },
    Cancel() {
      $("#myModal").modal("hide");
    },
    // 调用地图组件的搜索方法
    Search1() {
      this.search_flg = true;
      this.$refs.gmap.Search();
    },
    Search2() {
      this.search_flg = true;
      this.$refs.gmap2.Search();
    },
    // 调用子组件自定义方法，接收穿过来的值
    childname(data) {
      this.form.desc = data;
      // console.log("子组件传过来的值", data);
    },
    // ---------------------------------------2.Account页面--------------------------------------------
    // 点击修改按钮将对应的数据填充上去
    Edit3(item) {
      let params = {
        username: localStorage.getItem("user"),
        password: prompt("Please enter your password")
      };
      PasswordVerily(params).then(e => {
        if (e.data != "ok") {
          alert(e.data);
        } else {
          $("#myModal-3").modal("show");
          this.formAccount = item;
        }
      });
    },
    Delete3() {
      sessionStorage.setItem("activeName", this.activeName);
      this.activeName = "second";
      // 点击Edit按钮已经将本条对应数据填充上去了
      let params = { account: this.formAccount.account };
      onDelete_user(params).then(e => {
        alert(e.data);
        this.reload();
        this.activeName = "second";
      });
      $("#myModal-3").modal("hide");
    },
    Save3() {
      sessionStorage.setItem("activeName", this.activeName);
      let params = {
        account: this.formAccount.account,
        grade: this.formAccount.grade,
        password: this.formAccount.password,
        phone: this.formAccount.phone,
        email: this.formAccount.email,
        site: this.formAccount.site
      };
      // console.log('000000000000000000000000',params);
      onModify_user_put(params).then(e => {
        alert(e.data);
        this.reload();
      });
      $("#myModal-3").modal("hide");
    },
    Cancel3() {
      $("#myModal-3").modal("hide");
    },
    // ---添加设备信息 中的save delete cancel按钮---
    Clear2() {
      this.formAccount = {};
    },
    Save2() {
      sessionStorage.setItem("activeName", this.activeName);
      let formData = {
        account: this.formAccount.account,
        grade: this.formAccount.grade,
        password: this.formAccount.password,
        phone: this.formAccount.phone,
        email: this.formAccount.email,
        site: this.formAccount.site
      };
      // console.log(formData);
      newUser(formData).then(e => {
        alert(e.data);
        this.reload();
      });
      $("#myModal-2").modal("hide");
    },
    Cancel2() {
      $("#myModal-2").modal("hide");
    },
    // ---------------------------------------3.IP Cameras页面--------------------------------------------
    Edit5(item) {
      if (localStorage.getItem("grade") == "Administrator") {
        this.formCameras = item;
      } else {
        alert("You do not have permission");
      }
    },
    Delete5() {
      sessionStorage.setItem("activeName", this.activeName);
      // 点击Edit按钮已经将本条对应数据填充上去了
      let params = { site: this.formCameras.site };
      cameras_delete(params).then(e => {
        alert(e.data);
        this.reload();
      });
      $("#myModal-5").modal("hide");
    },
    Save5() {
      sessionStorage.setItem("activeName", this.activeName);
      let formData = {
        site: this.formCameras.site,
        location: this.formCameras.location,
        camerasAdds: this.formCameras.camerasAdds
      };
      // console.log(formData);
      cameras_update(formData).then(e => {
        alert(e.data);
        this.reload();
      });
      $("#myModal-5").modal("hide");
    },
    Cancel5() {
      $("#myModal-5").modal("hide");
    },
    // ---添加设备信息 中的save delete cancel按钮---
    Clear4() {
      this.formCameras = {};
    },
    Save4() {
      sessionStorage.setItem("activeName", this.activeName);
      let formData = {
        site: this.formCameras.site,
        location: this.formCameras.location,
        camerasAdds: this.formCameras.camerasAdds
      };
      // console.log(formData);
      cameras_post(formData).then(e => {
        alert(e.data);
        this.reload();
      });
      $("#myModal-4").modal("hide");
    },
    Cancel4() {
      $("#myModal-4").modal("hide");
    },
    // ---------------------------------------4.E-Mail页面--------------------------------------------
    E_Edit() {
      if (localStorage.getItem("grade") == "Administrator") {
        this.fontstyle.color = "rgb(160, 158, 158)";
        this.disabled = false;
        this.password = "";
        this.showEdit = false;
        this.showCancel = true;
      } else {
        alert("You do not have permission");
      }
    },
    E_Cancel() {
      this.showEdit = true;
      this.showCancel = false;
      // 将获取到的activeName保存到session，达到局部刷新，自动跳转到当前页面。
      sessionStorage.setItem("activeName", this.activeName);
      this.reload();
    },
    E_Save() {
      sessionStorage.setItem("activeName", this.activeName);
      if (this.fontstyle.color == "") {
        alert("No data has been modified");
      } else {
        let params = {
          server: this.email_list.server,
          port: this.email_list.port,
          method: this.email_list.method,
          account: this.email_list.account,
          password: this.email_list.password,
          grade: localStorage.getItem("grade")
        };
        console.log(params);
        
        email_update(params).then(e => {
          alert(e.data);
          this.reload();
        });
      }
    },
    E_Send() {
      if (localStorage.getItem("grade") == "Administrator") {
        let params = {
          test: this.test
        };
        console.log(params);
        email_test(params).then(e => {
          alert(e.data);
        });
      } else {
        alert("You do not have permission");
      }
    },
    // ---------------------------------------5.System Backup页面--------------------------------------------
    Backup() {
      if (localStorage.getItem("grade") == "Administrator") {
        let backup = document.getElementById("box1");
        // console.log(backup.checked)
        if (backup.checked == true) {
          let params = { user: localStorage.getItem("grade") };
          // console.log(params)
          sql_post(params).then(e => {
            // console.log(e.data);
            // Blob代表二进制类型的大对象,来接收文件流
            const blob = new Blob([e.data]);
            const a = document.createElement("a");
            // 生成文件路径
            let href = window.URL.createObjectURL(blob);
            // console.log(href);
            a.href = href;
            // console.log(e.headers);
            a.download = decodeURIComponent("dump.sql");
            // 利用a标签做下载
            document.body.appendChild(a);
            a.click();
            document.body.removeChild(a);
            window.URL.revokeObjectURL(href);
          });
        } else {
          alert("Please check the");
        }
      } else {
        alert("You do not have permission");
      }
    },
    Upgrade() {
      //
      let file = this.$refs.inputUrl.files[0];
      console.log(file);
      let formdata = new FormData();
      formdata.append("file", file);
      console.log(formdata);
      sql_put(formdata).then(e => {});
    },
    // ---------------------------------------7.Operating Data页面--------------------------------------------
    Search() {
      if (localStorage.getItem("grade") == "Administrator") {
        let s = new Date(this.value1[0]);
        let e = new Date(this.value1[1]);
        let params = {
          user: this.username,
          start_time:
            s.getFullYear() +
            "-" +
            (s.getMonth() + 1) +
            "-" +
            s.getDate() +
            " " +
            s.getHours() +
            ":" +
            s.getMinutes() +
            ":" +
            s.getSeconds(),
          end_time:
            e.getFullYear() +
            "-" +
            (e.getMonth() + 1) +
            "-" +
            e.getDate() +
            " " +
            e.getHours() +
            ":" +
            e.getMinutes() +
            ":" +
            e.getSeconds()
        };
        userstatus_post(params).then(e => {
          this.user_status_list = e.data;
        });
      } else {
        alert("You do not have permission");
      }
    }
  },
  computed: {},
  watch: {}
};
</script>
