import Vue from 'vue'
import Router from 'vue-router'

import Main from '@/components/Main'
import Login from '@/components/Login'
import SetDetail from '@/components/SetDetail'
import History from '@/components/History'
import Configuration from '@/components/Configuration'
import Test from '@/components/test'
import Tan from '@/components/tan'
import Mshow from '@/components/Mshow'
import EchartS from '@/components/Global_echarts'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/login',
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/Main',
      name: 'Overview',
      component: Main
    },
    {
      path: '/HistoryData',
      name: 'History',
      component: History
    },
    {
      path: '/SiteDetail',
      name: 'Site Detail',
      component: SetDetail
    },
    {
      path: '/configuration',
      name: 'Configuration',
      component: Configuration
    },


    {
      path: '/test',
      name: 'test',
      component: Test
    },
    {
      path: '/tan',
      name: 'tan',
      component: Tan
    },
    {
      path: '/mshow',
      name: 'Mshow',
      component: Mshow
    },
    {
      path: '/echerts',
      name: 'EchartS',
      component: EchartS
    }
  ]
})



