import Vue from "vue"
import Vuex from "vuex"
Vue.use(Vuex)
// 1.状态列表
const module_site_status = {
    state: {
        // 值存储
        site_status: ''
    },
    mutations: {
        // 存储
        show_site_status(state, site_status) {
            state.site_status = site_status
        }
    },
    getters: {
        //  getters是store 的计算属性
        get_site_status: state => {
            return state.site_status
        }
    }
}
//2. 告警列表
const module_warning_list = {
    state: {
        // 值存储
        warning_list: ''
    },
    mutations: {
        // 存储
        show_warning_list(state, warning_list) {
            state.warning_list = warning_list
        }
    },
    getters: {
        //  getters是store 的计算属性
        get_warning_list: state => {
            return state.warning_list
        }
    }
}
// 数据详情
const module_details_list = {
    state: {
        // 值存储
        details_list: ''
    },
    mutations: {
        // 存储
        show_details_list(state, details_list) {
            state.details_list = details_list
        }
    },
    getters: {
        //  getters是store 的计算属性
        get_details_list: state => {
            return state.details_list
        }
    }
}
// 4.详情列表
const module_details_data = {
    state: {
        // 值存储
        details_data: ''
    },
    mutations: {
        // 存储
        show_details_data(state, details_data) {
            state.details_data = details_data
        }
    },
    getters: {
        //  getters是store 的计算属性
        get_details_data: state => {
            return state.details_data
        }
    }
}
// --------------------------------------------
const store = new Vuex.Store({
    modules: {
        site_status: module_site_status,
        warning_list: module_warning_list,
        details_list: module_details_list,
        details_data: module_details_data
    }
})
export default store


