<template>
  <el-form
    :model="ruleForm2"
    :rules="rules2"
    ref="ruleForm2"
    label-width="0px"
    class="demo-ruleForm login-container"
  >
    <h3 class="title">Monitoring system</h3>
    <el-form-item prop="account">
      <el-input type="text" v-model="ruleForm2.account" auto-complete="off" placeholder="username"></el-input>
    </el-form-item>
    <el-form-item prop="checkPass">
      <el-input
        type="password"
        v-model="ruleForm2.checkPass"
        auto-complete="off"
        placeholder="passworld"
      ></el-input>
    </el-form-item>
    <!--  复选框里只要有checked属性，不管是否为其赋值，结果为空或true或false或任意值，均为选中状态 checked来控制复选框是否选中 -->
    <el-checkbox v-model="checked" checked class="remember">Remember password</el-checkbox>
    <el-form-item style="width:100%;">
      <!-- 父组件想在子组件上   监听自己的click 的话，需要加上native修饰符，prevent阻止默认事件的。 -->
      <el-button
        type="primary"
        style="width:100%;"
        @click="handleSubmit2"
        :logining="logining"
      >Sign in</el-button>
      <!-- <el-button @click.native.prevent="handleReset2">重置</el-button> -->
    </el-form-item>
  </el-form>
</template>

<script>
import { requestLogin } from "../Api/api";
export default {
  data() {
    return {
      // 0、在验证登入的时候判断
      logining: false,
      // 1、ruleForm2是表单的数据对象
      ruleForm2: {
        // 账户
        account: "",
        // 验证通过过
        checkPass: ""
      },
      // 2、element中Form组件提供了表单验证的功能，只需要通过 rules 属性传入约定的验证规则
      rules2: {
        account: [
          { required: true, message: "please enter account", trigger: "blur" },
          { max: 50, message: "Cannot exceed 50 characters" }
          //{ validator: validaePass }
        ],
        checkPass: [
          {
            required: true,
            message: "Please enter your password",
            trigger: "blur"
          },
          { max: 50, message: "Cannot exceed 50 characters" }
          //{ validator: validaePass2 }
        ]
      },
      // 3、记住密码
      checked: true
    };
  },
  methods: {
    handleSubmit2(ev) {
      // validate这里是校验的输入框的完整性，还有更高级用法vue-validator（vue验证器）组件
      this.$refs.ruleForm2.validate(valid => {
        // console.log(valid)
        if (valid) {
          this.logining = true;
          //nprogress页面加载进度条  需要安装
          // NProgress.start();
          let loginParams = {
            username: this.ruleForm2.account,
            password: this.ruleForm2.checkPass
          };
          requestLogin(loginParams).then(data => {
            let data_type = data.data;
            this.logining = false;
            // console.log("用户登入", data_type);
            // NProgress.done();
            // let { token, user_id, username } = data;
            if (data_type.user_id) {
              // 如果user_id存在，将用户名和等级和ip 存入到sessioStorage
              localStorage.setItem("user", data_type.username);
              localStorage.setItem("grade", data_type.grade);
              localStorage.setItem("u_flg", data_type.u_flg);
              localStorage.setItem("ip", JSON.stringify(data_type.ip));
              // console.log(typeof JSON.parse(localStorage.getItem("ip")));
              // console.log(localStorage.getItem("ip"));
              // console.log(JSON.parse(localStorage.getItem("ip")));
              // 取值
              // console.log(localStorage.getItem("user"))
              // push是用来跳转的
              this.$router.push({ path: "/main" });
            } else {
              // Element 注册了一个$message方法用于调用 从顶部出现，3 秒后自动消失
              this.$message({
                message: "wrong user name or password",
                type: "error"
              });
            }
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    }
  }
};
</script>
<style scoped lang='less'>
.login-container {
  // box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);
  -webkit-border-radius: 5px;
  border-radius: 5px;
  -moz-border-radius: 5px;
  background-clip: padding-box;
  margin: 180px auto;
  width: 350px;
  padding: 35px 35px 15px 35px;
  background: #fff;
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
  .title {
    margin: 0px auto 40px auto;
    text-align: center;
    color: #505458;
  }
  .remember {
    margin: 0px 0px 35px 0px;
  }
}
</style>


