      myChart.setOption(option);
    },
    // 3、export导出按钮
    export2Excel() {
      require.ensure([], () => {
        const { export_json_to_excel } = require("../vendor/Export2Excel");
        const tHeader = [
          "Site",
          "Equipment",
          "Parameter",
          "Alarm",
          "Value",
          "Unit",
          "Alarm Level",
          "Event Time"
        ];
        const filterVal = [
          "site",
          "equipment",
          "parameter",
          "alarm",
          "value",
          "unit",
          "level",
          "time"
        ];
        const list = this.alarm_list;
        const data = this.formatJson(filterVal, list);
        export_json_to_excel(tHeader, data, "History Data");
      });
    },
    formatJson(filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]));
    },
    // 4个信号值
    // signal_1的值
    Signal_site(signal_site) {
      this.signal1_equipment = "";
      this.signal1_parameter = "";
      let params = { site: signal_site };
      signal_post(params).then(e => {
        this.signal1_equipment_list = e.data;
      });
    },
    Signal_equipment(signal_equipment) {
      this.signal1_parameter = "";
      let params = {
        site: this.signal1_site,
        equipment: signal_equipment
      };
      signal_post(params).then(e => {
        this.signal1_parameter_list = e.data;
      });
    },
    // signal 2的值
    Signal2_site(signal2_site) {
      this.signal2_equipment = "";
      this.signal2_parameter = "";
      let params = { site: signal2_site };
      signal_post(params).then(e => {
        this.signal2_equipment_list = e.data;
      });
    },
    Signal2_equipment(signal2_equipment) {
      this.signal2_parameter = "";
      let params = {
        site: this.signal2_site,
        equipment: signal2_equipment
      };
      signal_post(params).then(e => {
        this.signal2_parameter_list = e.data;
      });
    },
    // signal 3的值
    Signal3_site(signal3_site) {
      this.signal3_equipment = "";
      this.signal3_parameter = "";
      let params = { site: signal3_site };
      signal_post(params).then(e => {
        this.signal3_equipment_list = e.data;
      });
    },
    Signal3_equipment(signal3_equipment) {
      this.signal3_parameter = "";
      let params = {
        site: this.signal3_site,
        equipment: signal3_equipment
      };
      signal_post(params).then(e => {
        this.signal3_parameter_list = e.data;
      });
    },
    // signal 4的值
    Signal4_site(signal4_site) {
      this.signal4_equipment = "";
      this.signal4_parameter = "";
      let params = { site: signal4_site };
      signal_post(params).then(e => {
        this.signal4_equipment_list = e.data;
      });
    },
    Signal4_equipment(signal4_equipment) {
      this.signal4_parameter = "";
      let params = {
        site: this.signal4_site,
        equipment: signal4_equipment
      };
      signal_post(params).then(e => {
        this.signal4_parameter_list = e.data;
      });
    },
    Obtain() {
      // 在重复操作请求时候要清空这里的值，不然会报错。
      this.e_date1 = [];
      this.e_date2 = [];
      this.e_date3 = [];
      this.e_date4 = [];
      this.e_data1 = [];
      this.e_data2 = [];
      this.e_data3 = [];
      this.e_data4 = [];
      this.e_sig1_name = "";
      this.e_sig2_name = "";
      this.e_sig3_name = "";
      this.e_sig4_name = "";
      this.e_unit1 = "";
      this.e_unit2 = "";
      this.e_unit3 = "";
      this.e_unit4 = "";

      this.signal_list = [];
      this.echart_signal1 = [];
      this.echart_signal2 = [];
      this.echart_signal3 = [];
      this.echart_signal4 = [];
      this.echart_time = [];
      let s = new Date(this.value1[0]);
      let e = new Date(this.value1[1]);
      let params = {
        site1: this.signal1_site,
        equipment1: this.signal1_equipment,
        parameter1: this.signal1_parameter,
        site2: this.signal2_site,
        equipment2: this.signal2_equipment,
        parameter2: this.signal2_parameter,
        site3: this.signal3_site,
        equipment3: this.signal3_equipment,
        parameter3: this.signal3_parameter,
        site4: this.signal4_site,
        equipment4: this.signal4_equipment,
        parameter4: this.signal4_parameter,
        start_time:
          s.getFullYear() +
          "-" +
          (s.getMonth() + 1) +
          "-" +
          s.getDate() +
          " " +
          s.getHours() +
          ":" +
          s.getMinutes() +
          ":" +
          s.getSeconds(),
        end_time:
          e.getFullYear() +
          "-" +
          (e.getMonth() + 1) +
          "-" +
          e.getDate() +
          " " +
          e.getHours() +
          ":" +
          e.getMinutes() +
          ":" +
          e.getSeconds()
      };
      console.log(params);
      signal_post(params).then(e => {
        this.signal_list = JSON.parse(e.data);
        // console.log(e.data);
        // console.log(typeof(e.data)); // string字符串类型
        // 接受传过来的signal和time值
        this.echart_signal1 = this.signal_list.signal1;
        this.echart_signal2 = this.signal_list.signal2;
        this.echart_signal3 = this.signal_list.signal3;
        this.echart_signal4 = this.signal_list.signal4;
        console.log(this.echart_signal1);
        console.log(typeof this.echart_signal2);
        console.log(this.echart_signal3);
        console.log(this.echart_signal4);

        this.echart_signal1.forEach(e => {
          this.e_date1.push(e.time);
          this.e_data1.push(e.value);
        });
        this.echart_signal2.forEach(e => {
          this.e_date2.push(e.time);
          this.e_data2.push(e.value);
        });
        this.echart_signal3.forEach(e => {
          this.e_date3.push(e.time);
          this.e_data3.push(e.value);
        });
        this.echart_signal4.forEach(e => {
          this.e_date4.push(e.time);
          this.e_data4.push(e.value);
        });
        if (this.echart_signal1.length > 0) {
          console.log(11111111);
          this.e_sig1_name =
            String(this.echart_signal1[0].equipment) +
            "/" +
            this.echart_signal1[0].parameter;
          this.e_unit1 = String(this.echart_signal1[0].unit);
          // console.log(this.e_sig1_name);
          // console.log(typeof this.e_unit1);
        } else {
          console.log(222222)
          this.e_sig1_name = "";
          this.e_unit1 = "";
        }
        if (this.echart_signal2.length > 0) {
          console.log(333333);
          this.e_sig2_name =
            String(this.echart_signal2[0].equipment) +
            "/" +
            this.echart_signal2[0].parameter;
          // console.log(this.echart_signal2.length);
          this.e_unit2 = String(this.echart_signal2[0].unit);
          // console.log(this.e_sig2_name);
          // console.log(typeof this.e_unit2);
        } else {
          console.log(44444);
          this.e_sig2_name = "";
          this.e_unit2 = "";
        }
        if (this.echart_signal3.length > 0) {
          console.log(55555);
          this.e_sig3_name =
            String(this.echart_signal3[0].equipment) +
            "/" +
            this.echart_signal3[0].parameter;
          this.e_unit3 = this.echart_signal3[0].unit;
        } else {
          console.log(66666);
          this.e_sig3_name = "";
          this.e_unit3 = "";
        }
        if (this.echart_signal4.length > 0) {
          this.e_sig4_name =
            String(this.echart_signal4[0].equipment) +
            "/" +
            this.echart_signal4[0].parameter;
          this.e_unit4 = this.echart_signal4[0].unit;
        } else {
          this.e_sig4_name = "";
          this.e_unit4 = "";
        }
        // 这里必须要调用一下，不然echart没有展示
        console.log(7777777);
        this.EchartsLine();
        // this.reload();
      });
    }
  }
};
</script>
<style scoped>
