}
.history .but-class button {
  font-size: 16px;
  border-radius: 10px;
  border: 1px solid #0080ff;
  color: #0080ff;
  background-color: transparent;
  outline: none;
}
/* 设置高度和背景色 */
.history /deep/ .el-tabs__header {
  margin: 0px;
  height: 60px;
