}
/* 修改切换栏的 字体大小 */
.history /deep/ .el-tabs__item {
  /* padding: 0, 8px; */
  font-size: 16px;
}
/* 修改切换栏 选中后的字体颜色和加粗展示 */
.history /deep/ .el-tabs__item.is-active {
  color: black;
  font-weight: bold;
}
/* 修改鼠标触碰展示的颜色 */
.history /deep/ .el-tabs__item:hover {
  color: #000;
}
/* -----------3个页面切换--------- */
