}
/* -----------中间页面---------- */
.history .select-3 {
  width: 100%;
  height: 370px;
  margin: 0;
  padding: 0 10px 0 10px;
}
.history .select-3 .select-3-1 {
  border-bottom: 1px solid #ededed;
