}
/* 搜索属性的修改 */
.history .select-3 /deep/ .el-select {
  width: 16%;
}
.history .select-3 /deep/ .el-input {
  width: 104%;
}
/* 日期选择框的宽度 */
.history .select-3 /deep/ .el-date-editor--daterange.el-input__inner {
  width: 24%;
}
.history .select-3 /deep/ .el-date-editor .el-range-separator {
  width: 10%;
}
/* 修改下拉框的圆角 */
.history .select-3 /deep/ .el-input__inner {
