}
.history .select-3 /deep/ .el-select .el-input__inner:focus {
  border-color: #c0c4cc;
}
/* serach搜索按钮 */
.history .select-3-1 button {
  font-size: 16px;
  border-radius: 10px;
  border: 1px solid #0080ff;
  color: #0080ff;
  background-color: transparent;
  outline: none;
  float: right;
  margin: 8px 20px 0 0;
}
/* -----------右边页面---------- */
.history .select-4 {
  width: 100%;
  height: 370px;
  padding: 0 10px 0 10px;
}
.history .select-4 .sig-1,
.sig-2,
.sig-3,
.sig-4,
.sig-5 {
  padding-top: 0.1em;
  text-align: center;
  position: relative;
}
.history .select-4 .sig-s {
  /* 去掉select的边框和点击时的蓝色边框 */
  border: none;
  outline: none;
  /* 去掉select下拉的小三角 */
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;

  height: 2.2em;
  width: 6em;
  margin-right: -5px;
  border-top-left-radius: 2.2em;
  border-bottom-left-radius: 2.3em;
}
/* 最后的搜索样式不同 */
