<template>
  <div id="app" class="container">
    <!-- 导航条 -->
    <glo></glo>
    <!-- 搜索条和信息条 -->
    <div class="col-xs-12 col-sm-12 col-md-6 col-lg-6 background">
      <div class="background-margin">
        <!-- 右箭头 -->
        <span class="glyphicon glyphicon-chevron-right"></span>
        <!-- 下拉框 -->
        <button
          type="button"
          class="bt_style"
          id="dropdownMenu1"
          data-toggle="dropdown"
          aria-haspopup="true"
          aria-expanded="true"
        >
          {{this.value}}
          <!-- 向下箭头标签 -->
          <span class="glyphicon glyphicon-triangle-bottom"></span>
        </button>
        <ul class="dropdown-menu">
          <li v-for="(item, index) in site_list" :key="index">
            <a @click="click(item.site)" href="#">{{ item.site}}</a>
          </li>
        </ul>
        <!-- 2、输入搜索框 实现候选关键字(模糊搜索) 参考element中的 input输入框中，带输入建议-->
        <el-autocomplete
          v-model="state2"
          :fetch-suggestions="querySearch"
          placeholder="Search Site"
          :trigger-on-focus="false"
          @select="handleSelect"
        ></el-autocomplete>
      </div>
    </div>
    <div class="col-xs-12 col-sm-12 col-md-6 col-lg-6 background2">
      <div class="background-margin">
        <ul class="ul-style">
          <li>{{this.details_list.site}} |</li>
          <li>{{this.details_list.location}} |</li>
          <li>{{this.details_list.type}} |</li>
          <li>{{this.details_list.ip}} |</li>
          <li class="HiddenDisplay">{{this.details_list.manager}}</li>
        </ul>
      </div>
