    </div>
    <div class="col-xs-12 col-sm-12 col-md-12 col-lg-12 params" id="params-s-3">
      <div style="height:50px;position:relative;border-bottom:1px solid #e6e6e6">
        <p>Current Parameters</p>
        <div style="float:right; position:absolute; left:92%;top:23%">
          <button
            @click="ButtonDisplay()"
