    </div>
    <!-- 第二块 -->
    <div class="col-xs-12 col-sm-12 col-md-3 col-lg-3 power" id="power-s">
      <div class="power-1">
        <p>Power System</p>
      </div>
      <div class="power-2">
        <p :style="{'font-size':'14px','color':'#86898f','padding':'5px 0 0 10px'}">
