        </p>
        <!-- 加载矢量图svg -->
        <icon class="svg-1" name="kaiguan"></icon>
        <icon class="svg-1-1" name="kaiguan"></icon>
        <icon class="svg-2" name="nibianqiline"></icon>
        <icon class="svg-3" name="panglu"></icon>
        <icon class="svg-4" name="xudianchi"></icon>
        <icon class="svg-5" name="zhengliuqil"></icon>
        <icon class="svg-7" name="jiantou"></icon>
        <!-- 在js中使用svg -->
        <svg xmlns="http://www.w3.org/2000/svg" width="100%" height="100%">
          <!-- 市电 7条线 -->
          <polyline points="30 72 56 72" fill="none" stroke="#c0c0c0" stroke-width="2" id="sd-1" />
          <polyline points="69 72 79 72" fill="none" stroke="#c0c0c0" stroke-width="2" id="sd-2" />
          <polyline points="79 72 89 72" fill="none" stroke="#c0c0c0" stroke-width="2" id="sd-3" />
          <polyline
            points="108 72 133 72"
            fill="none"
            stroke="#c0c0c0"
            stroke-width="2"
            id="sd-4"
          />
          <polyline
            points="133 72 158 72"
            fill="none"
            stroke="#c0c0c0"
            stroke-width="2"
