          />
          <polyline
            points="193 72 205 72"
            fill="none"
            stroke="#c0c0c0"
            stroke-width="2"
            id="sd-7"
          />
          <!-- 旁路 -->
          <polyline
            points="79 72 79 6 120 6"
            fill="none"
            stroke="#c0c0c0"
            stroke-width="2"
            id="pl-1"
          />
          <polyline
            points="146 6 193 6 193 72"
            fill="none"
            stroke="#c0c0c0"
            stroke-width="2"
            id="pl-2"
          />
          <!-- 电池 -->
          <polyline
            points="133 72 133 102"
            fill="none"
            stroke="#c0c0c0"
            stroke-width="2"
            id="dc-1"
          />
          <!-- 文字 -->
          <text x="50" y="90" font-size="12" text-anchor="middle">Input</text>
          <text x="218" y="90" font-size="12" text-anchor="middle">Output</text>
          <text
            x="152"
            y="115"
            font-size="12"
            font-style="italic"
            font-family="SimHei"
            text-anchor="middle"
          >BC</text>
          <text x="133" y="26" font-size="10" text-anchor="middle">StaticSwitch</text>
          <text x="100" y="60" font-size="10" text-anchor="middle">Rectifier</text>
          <text x="170" y="55" font-size="10" text-anchor="middle">Inverter</text>
        </svg>
        <!-- 左右2个方形显示数据 -->
        <!-- 左边 -->
        <div class="power-2-1">
          <div>
            <span class="sp1">{{this.input_vol}}</span>
            <span class="sp2">VAC</span>
          </div>
          <div>
            <span class="sp1">{{this.input_fre}}</span>
            <span class="sp2">HZ</span>
          </div>
          <div>
            <span class="sp1">{{ this.input_phase}}</span>
            <span class="sp2">Phase</span>
          </div>
        </div>
        <!-- 右边 -->
        <div class="power-2-2">
          <div>
            <span class="sp1">{{this.output_vol}}</span>
            <span class="sp2">VAC</span>
          </div>
          <div>
            <span class="sp1">{{this.output_fre}}</span>
            <span class="sp2">HZ</span>
          </div>
          <div>
            <span class="sp1">{{ this.output_phase}}</span>
            <span class="sp2">Phase</span>
          </div>
        </div>
      </div>
      <div class="power-3">
        <div class="power-e">
          <p
            :style="{'font-size':'14px','text-align':'center','color':'#86898f','padding':'0','margin':'0 0 -10px 0'}"
          >Power</p>
          <div
            id="EchartPower"
            :style="{'height':'150px','width':'137.5px','padding':'0','margin':'0'}"
          ></div>
          <p
            :style="{'font-size':'14px','text-align':'center','color':'#86898f','padding':'0','margin':'-35px 0 0 0'}"
          >{{this.meter_power}} KW</p>
        </div>
        <div class="load-e">
          <p
            :style="{'font-size':'14px','text-align':'center','color':'#86898f','padding':'0','margin':'0 0 -10px 0'}"
          >Load</p>
          <div
            id="EchartLoad"
            :style="{'height':'150px','width':'137.5px','padding':'0','margin':'0'}"
          ></div>
          <p
            :style="{'font-size':'14px','text-align':'center','color':'#86898f','padding':'0','margin':'-35px 0 0 0'}"
          >{{this.ups_load}} %</p>
        </div>
      </div>
    </div>
    <!-- 第三块 -->
    <div class="col-xs-12 col-sm-12 col-md-3 col-lg-3 environmental" id="environmental-s">
      <div class="environmental-1">
        <p>Environmental System</p>
      </div>
      <div class="environmental-2">
        <p :style="{'font-size':'14px','color':'#86898f','padding':'5px 0 0 10px'}">
          <button
            @click="DetailsTemp()"
            style=" border: 1px soild #fff;background-color: transparent;outline: none; height:2.3rem"
          >Temperature</button>
        </p>
        <!-- echarts图表 -->
        <div id="EchartLine" :style="{'height':'140px','weight':'275px'}"></div>
      </div>
      <div class="environmental-3">
        <p :style="{'font-size':'13px', 'color':'#86898f','padding':'5px 0 0 10px'}">
          <button
            @click="DetailsCooling()"
            style="position: relative; z-index: 99;border: 1px soild #fff;background-color: transparent;outline: none; height:2.3rem"
          >Cooling</button>
        </p>
        <div>
          <span class="sp1">Return Temp</span>
          <span class="sp2">{{this.Return_Temp}} ℃</span>
        </div>
        <div>
          <span class="sp1">Discharge Temp</span>
          <span class="sp2">{{this.Discharge_Temp}} ℃</span>
        </div>
        <div>
          <span class="sp1">Humidity</span>
          <span class="sp2">{{this.AC_Humidity}}% RH</span>
        </div>
        <div>
          <span class="sp1">AC Mode</span>
          <span class="sp2">{{this.AC_Mode}}</span>
        </div>
        <div>
          <span class="sp1">Leaking Sensor</span>
          <span class="sp2">{{this.Leaking}}</span>
        </div>
      </div>
    </div>
    <!-- 第四块 -->
    <div class="col-xs-12 col-sm-12 col-md-3 col-lg-3 security" id="security-s">
      <div class="security-1">
        <p>Security System</p>
      </div>
      <div class="security-2">
        <!-- 这个换行符作用 充当缩进用 -->
        <br />
        <div>
          <span class="sp1">Door</span>
          <span class="sp2">Closed</span>
        </div>
        <div>
          <span class="sp1">Video Link</span>
          <span class="sp2">Click</span>
        </div>
      </div>
    </div>
    <!-- 第五块 -->
    <div class="col-xs-12 col-sm-12 col-md-3 col-lg-3 fire" id="fire-s">
      <div class="fire-1">
        <p>Fire System</p>
      </div>
      <div class="fire-2">
        <!-- 这个换行符作用 充当缩进用 -->
        <br />
        <div>
          <span class="sp1">Early Detection</span>
          <span class="sp2">OK</span>
        </div>
        <div>
          <span class="sp1">Fire Sensor</span>
          <span class="sp2">OK</span>
        </div>
        <div>
          <span class="sp1">Fire Gas</span>
          <span class="sp2">OK</span>
        </div>
      </div>
    </div>
    <!-- 页脚 -->
    <globar></globar>
  </div>
</template>

<script>
import { All_site } from "../Api/api";
import { mapState, mapGetters, mapMutations, mapActions } from "vuex";
export default {
  inject: ["reload"],
  data() {
    return {
      // 0、下拉选择value值的双向绑定
      value: "",
      // 1、获取MDC列表
      site_list: [],
      // 2、实现候选关键字功能
      restaurants: [],
      state2: "",
      // 3、右边详情栏，动态绑定
      details_list: [],
      data_list: [],
      // 4、告警列表
      alarm_list: [],
      // 5、实时数据字段
      temps: "",
      temps_list: [],
      time_temps: "",
      time_temps_list: [],
      Humidity: "",
      // ups状态信号值
      UPS_Signal: "",
      input_vol: "",
      output_vol: "",
      input_fre: "",
      output_fre: "",
      input_phase: "",
      output_phase: "",
      meter_power: "",
      ups_load: "",
      Return_Temp: "",
      Discharge_Temp: "",
      AC_Humidity: "",
      AC_Mode: "",
      Leaking: "",
      // EchartLine

      // 6、params详情页面
      params_list: [],
      ups_list: [],
      temps_Hum_list: [],
      ac_list: [],

      //显示和隐藏
      showBox: true
    };
  },
  created() {
    // 1、发送请求获取MDC列表
    let params = {
      grade: localStorage.getItem("grade"),
      username: localStorage.getItem("user")
    };
    All_site(params).then(e => {
      this.site_list = e.data;
      this.value = this.site_list[0].site;
      this.details_list = this.site_list.find(item => {
        return item.site == this.value;
      });
      // 2、候选关键字功能 如果不放在这个后面，初始数据时候，会没有值
      this.restaurants = this.loadAll();
    });
    // 3.获取导航栏或者地图坐标点，传过来的路由参数
    let datas = this.$route.params.site
    // console.log('1111111111111',datas)
    // this.click(datas)
  },
  mounted() {
    // 3、EcharLine
    this.EchartsLine();
    // 4、仪表盘Power
    this.EchartsPower();
    // 5、仪表盘Load
    this.EchartsLoad();
  },
  computed: {
    // 解包实时告警数据
    ...mapGetters(["get_warning_list"]),
    // 解包实时数据展示
    ...mapGetters(["get_details_list"]),
    // 解包实时数据列表 UPS
    ...mapGetters(["get_details_data"])
  },
  watch: {
    // 解包实时告警数据
    get_warning_list(val) {
      // 1.清空告警列表 不然会重复添加
      this.alarm_list = [];
      // 2.得到右侧告警数据列表
      val.forEach(item => {
        if (item.site == this.value) {
          this.alarm_list.push(item);
        }
      });
    },
    // 解包实时数据展示
    get_details_list(val) {
      // this.value是 唯一对应的MDC
      // console.log(this.value);
      val.forEach(item => {
        if (item.site == this.value) {
          // console.log(item);
          if (item.name == "temps") {
            this.temps = item.temps;
            this.temps_list.push(this.temps);
            this.time_temps = item.time;
            this.time_temps_list.push(this.time_temps);
          } else if (item.name == "UPS_Signal") {
            this.UPS_Signal = item.UPS_Signal;
            // console.log(typeof(item.UPS_Signal)) number类型
            let sd_1 = document.getElementById("sd-1");
            let sd_2 = document.getElementById("sd-2");
            let sd_3 = document.getElementById("sd-3");
            let sd_4 = document.getElementById("sd-4");
            let sd_5 = document.getElementById("sd-5");
            let sd_6 = document.getElementById("sd-6");
            let sd_7 = document.getElementById("sd-7");
            let pl_1 = document.getElementById("pl-1");
            let pl_2 = document.getElementById("pl-2");
            let dc_1 = document.getElementById("dc-1");
            if (item.UPS_Signal == 1) {
              sd_1.setAttribute("stroke", "#008000");
              sd_2.setAttribute("stroke", "#c0c0c0");
              sd_3.setAttribute("stroke", "#c0c0c0");
              sd_4.setAttribute("stroke", "#c0c0c0");
              sd_5.setAttribute("stroke", "#c0c0c0");
              sd_6.setAttribute("stroke", "#c0c0c0");
              sd_7.setAttribute("stroke", "#008000");
              pl_1.setAttribute("stroke", "#008000");
              pl_2.setAttribute("stroke", "#008000");
              dc_1.setAttribute("stroke", "#c0c0c0");
            } else if (item.UPS_Signal == 2) {
              sd_1.setAttribute("stroke", "#008000");
              sd_2.setAttribute("stroke", "#008000");
              sd_3.setAttribute("stroke", "#008000");
              sd_4.setAttribute("stroke", "#008000");
              sd_5.setAttribute("stroke", "#008000");
              sd_6.setAttribute("stroke", "#008000");
              sd_7.setAttribute("stroke", "#008000");
              pl_1.setAttribute("stroke", "#c0c0c0");
              pl_2.setAttribute("stroke", "#c0c0c0");
              dc_1.setAttribute("stroke", "#c0c0c0");
            } else if (item.UPS_Signal == 3) {
              sd_1.setAttribute("stroke", "#c0c0c0");
              sd_2.setAttribute("stroke", "#c0c0c0");
              sd_3.setAttribute("stroke", "#c0c0c0");
              sd_4.setAttribute("stroke", "#c0c0c0");
              sd_5.setAttribute("stroke", "#008000");
              sd_6.setAttribute("stroke", "#008000");
              sd_7.setAttribute("stroke", "#008000");
              pl_1.setAttribute("stroke", "#c0c0c0");
              pl_2.setAttribute("stroke", "#c0c0c0");
              dc_1.setAttribute("stroke", "#008000");
            } else {
              sd_1.setAttribute("stroke", "#c0c0c0");
              sd_2.setAttribute("stroke", "#c0c0c0");
              sd_3.setAttribute("stroke", "#c0c0c0");
              sd_4.setAttribute("stroke", "#c0c0c0");
              sd_5.setAttribute("stroke", "#c0c0c0");
              sd_6.setAttribute("stroke", "#c0c0c0");
              sd_7.setAttribute("stroke", "#c0c0c0");
              pl_1.setAttribute("stroke", "#c0c0c0");
              pl_2.setAttribute("stroke", "#c0c0c0");
              dc_1.setAttribute("stroke", "#c0c0c0");
            }
          } else if (item.name == "Humidity") {
            this.Humidity = item.Humidity;
          } else if (item.name == "input_vol") {
            this.input_vol = item.input_vol;
          } else if (item.name == "output_vol") {
            this.output_vol = item.output_vol;
          } else if (item.name == "input_fre") {
            this.input_fre = item.input_fre;
          } else if (item.name == "output_fre") {
            this.output_fre = item.output_fre;
          } else if (item.name == "input_phase") {
            this.input_phase = item.input_phase;
          } else if (item.name == "output_phase") {
            this.output_phase = item.output_phase;
          } else if (item.name == "meter_power") {
            this.meter_power = item.meter_power;
          } else if (item.name == "ups_load") {
