<template>
  <div id="app" class="container">
    <!-- 导航条 -->
    <glo></glo>
    <!-- 1、左边页面 设置背景色-->
    <div
      class="col-xs-12 col-sm-12 col-md-6 col-lg-6 dh-1"
      v-bind:style="{'padding-top':'20px','background':'#EDEDED'}"
    >
      <!-- 左边导航栏按钮  参考ellement中Tabs 标签页-->
      <div :style="{'position':'relative'}">
        <el-tabs v-model="activeName" @tab-click="handleClick">
          <el-tab-pane label="Map view" name="first">
            <!-- 地图组件 -->
            <div class="nav-map">
              <globalmap></globalmap>
            </div>
          </el-tab-pane>
          <el-tab-pane label="Sites view" name="third">
            <div>
              <div
                class="table"
                style="overflow-x:scroll; border:0px; height:370px; background:white"
              >
                <!-- <el-table :show-overflow-tooltip="true" :data="site_status" style="width: 100%">
                  <el-table-column prop="site" label="Site"></el-table-column>
                  <el-table-column prop="location" label="Location" ></el-table-column>
                  <el-table-column prop="status" label="Status"></el-table-column>
                  <el-table-column prop="manage" label="Manager"></el-table-column>
                </el-table>-->
                <table
                  class="table table-hover"
                  :show-overflow-tooltip="true"
                  style="overflow-x:scroll;"
                >
                  <thead>
                    <tr>
                      <th>Site</th>
                      <th>Location</th>
                      <th>Status</th>
                      <th>Manager</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(item, index) in site_status" :key="index">
                      <td>{{ item.site }}</td>
                      <td>{{ item.location }}</td>
                      <td>{{ item.status }}</td>
                      <td class="HiddenDisplay">{{ item.manage }}</td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </el-tab-pane>
        </el-tabs>
        <!-- 在父级div设置相对定位，在子级div设置绝对定位 -->
        <div :style="{'position':'absolute','left':'90px','top':'10px','z-index':'9999'}">|</div>
      </div>
    </div>

    <!-- 2、右边页面 设置背景色-->
    <div
      class="col-xs-12 col-sm-12 col-md-6 col-lg-6"
      v-bind:style="{'padding-top':'20px','background':'#EDEDED'}"
    >
      <!-- 边导航栏按钮 -->
      <div :style="{'position':'relative'}">
        <!-- 用相对定位，固定告警的位置 -->
        <div
          :style="{'position':'absolute','right':'27%','top':'8px','z-index':'9999','color':'red','font-weight':'bold'}"
        >
          <p>Critical alarm：{{ this.Critical.length}}</p>
        </div>
        <div
          :style="{'position':'absolute','right':'3%','top':'8px','z-index':'9999','color':'#D9D900','font-weight':'bold'}"
        >
          <p>General alarm：{{ this.General.length}}</p>
        </div>
        <!-- 告警列表 -->
        <el-tabs class="dh-2">
          <el-tab-pane label="Alarms View">
            <!-- 这是列表 -->
            <div
              class="table"
              style="overflow-x:scroll; border:0px; height:370px; background:white"
            >
              <table class="table table-hover" style="overflow-x:scroll;">
                <thead>
                  <tr>
                    <th>Site</th>
                    <th>Equipment</th>
                    <th>Alarm</th>
                    <th>Content</th>
                    <th>Level</th>
                    <th>time</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(item, index) in warning_list" :key="index">
                    <td>{{ item.site }}</td>
                    <td>{{ item.equipment }}</td>
                    <td>{{ item.alarm }}</td>
                    <td>{{ item.alarm_text }}</td>
                    <td>{{ item.level }}</td>
                    <td>{{ item.lssue_time }}</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </el-tab-pane>
        </el-tabs>
      </div>
    </div>
    <!-- 页脚 -->
    <globar></globar>
  </div>
</template>
<script>
import { websocket_get } from "../Api/api";
export default {
  inject: ["reload"], // 局部刷新
  data() {
    return {
      // 地图和列表切换 默认选择地图
      activeName: "first",
      showView: true,
      // 告警的数量
      Critical: [],
      General: [],
      // 告警列表
      warning_list: [],
      // 告警站点列表 通过warning_list去重得到
      site_status: [],

      // websocket建立的连接
      websock: null,
      // websocket 是否真正建立连接
      lockRconnect: false,
      // 30秒一次心跳
      timeout: 30 * 1000,
      // 心跳倒计时
      timeoutObj: null,
      //心跳倒计时
      serverTimeoutObj: null,
      // 断开重新连接倒计时
      timeoutnum: null
    };
  },

  created() {
    let params = { user: localStorage.getItem("user") };
    // 这是发送过去的get请求
    websocket_get(params).then(e => {
      // alert(e.data);  //返回的是查询到的ip列表
      this.initWebSocket("websocket"); // 无返回结果 这个请求只是去循环操作数据
      this.initWebSocket("querywebsocket"); // 有返回查询结果
      this.initWebSocket("GetWebsocket"); // 详情页面的实时告警
      this.initWebSocket("PostWebsocket"); // 详情的实时数据
    });
  },
  destroyed() {
    // this.websock.onclose(); // 离开路由之后断开websocket连接
  },
  mounted() {},
  methods: {
    // 地图和列表切换页面
    handleClick(tab, event) {
      // alert(tab, event);
    },
    // -------------websocket-----------

    initWebSocket(params) {
      //初始化weosocket
      const wsUrl = "ws://192.168.1.33:8000/" + params;
      // console.log(wsUrl);
      // 创建websocket实例
      this.websock = new WebSocket(wsUrl);
      // 一点建立了连接，websocket实例中就会触发open事件，通过send方法发送数据
      this.websock.onopen = this.websocketonopen(params);
      this.websock.onmessage = this.websocketonmessage;
      this.websock.onerror = this.websocketonerror;
      this.websock.onclose = this.websocketonclose;
    },
    websocketonopen(params) {
      //建立连接后就发送数据
      // 这里可以传输 用户名 参数 传递过去永远都是字符串类型
      let params_str = {
        ip: JSON.stringify(["192.168.1.175", "192.168.1.30"])
      }; //这是传递的格式
      // 前端传给后端都是字符串类型 JSON.stringify将收到的对象转换为字符串
      // 前端传给后端都是字符串类型 JSON.parse()将收到的字符串转化为对象

      //webSocket的readyState属性用来定义连接状态，该属性的值有下面几种：
      // 0 ：对应常量CONNECTING (numeric value 0)，
      // 正在建立连接连接，还没有完成。The connection has not yet been established.
      // 1 ：对应常量OPEN (numeric value 1)，
      // 连接成功建立，可以进行通信。The WebSocket connection is established and communication is possible.
      // 2 ：对应常量CLOSING (numeric value 2)
      // 连接正在进行关闭握手，即将关闭。The connection is going through the closing handshake.
      // 3 : 对应常量CLOSED (numeric value 3)
      // 连接已经关闭或者根本没有建立。The connection has been closed or could not be opened.
      if (this.websock.readyState === 1) {
        this.websock.send(params_str);
      }
      // 开启心跳
      this.start(params);
      // console.log("0:%s打开open开启心跳", params);
    },
    websocketonmessage(e) {
      // 得到返回参数中的url
      let url_str = e.currentTarget.url;
      // 通过正则获取参数
      let params_str = url_str.match(/[^\/]+$/)[0];
      //数据接收  里面会有多个不同的实时告警切换
      let redata = JSON.parse(e.data); // 一般传输的是字符串类型 JSON.parse() 方法将数据转换为 JavaScript 对象.
      // console.log(typeof redata); //传输过来的是string类型
      // console.log(redata); //转化后为object类型
      if (redata.main) {
        // console.log("show_warning_list:", redata.main.length);
        this.warning_list = redata.main;
        // 将告警列表存储vuex
        this.$store.commit("show_warning_list", this.warning_list);
        // console.log(this.warning_list);
        // 1、通过过滤得到告警的数量
        this.Critical = this.warning_list.filter(item => {
          return item.level == "Critical";
        });
        this.General = this.warning_list.filter(item => {
          return item.level == "General";
        });
        // 2、通过动态数据去重 得到 站点状态列表
        this.site_status = this.unique(this.warning_list);
        // manage_status.forEach(element => {
        //   console.log(typeof(element.manage))
        //   console.log(element.manage)
        //   element.manage.join(",")
        // });
        // console.log(manage_status);
        // console.log(this.site_status);
        // 3、保存数据到公共组件（vuex）
        // concat()合并列表 暂时不需要合并
        // this.warning_list3 = this.warning_list.concat(this.warning_list1);
        this.$store.commit("show_site_status", this.site_status);
        // console.log('===========>',this.$store.state.site_status);
      } else if (redata.details) {
        // console.log("show_details_list:", redata.details.length);
        // 这是各个点的实时数据
        this.$store.commit("show_details_list", redata.details);
        // console.log(typeof(redata.details))
        // console.log(redata.details)
      } else if (redata.details_data) {
        // 这是实时详情列表 details_data
        // console.log("show_details_data:", redata.details_data.length);
        this.$store.commit("show_details_data", redata.details_data);
      }
      // 4、收到服务器消息，心跳重置
      this.reset(params_str);
    },
    // 去重得到状态列表
    unique(warning_list) {
      const res = new Map();
      return warning_list.filter(
        a => !res.has(a.alarm_ip) && res.set(a.alarm_ip, 1)
      );
    },
    websocketonclose(e) {
      // 连接关闭
      let url_str = e.currentTarget.url;
      // 通过正则获取参数
      let params_str = url_str.match(/[^\/]+$/)[0];
      console.log("%s连接关闭:", params_str, e);
      // 连接错误重新连接websocket
      this.reconnect(params_str);
    },
    websocketonerror(e) {
      //连接错误
      let url_str = e.currentTarget.url;
      // 通过正则获取参数
      let params_str = url_str.match(/[^\/]+$/)[0];
      console.log("%s连接错误:", params_str, e);
      // 连接错误 从新连接
      this.reconnect(params_str);
    },
    reconnect(params) {
      if (this.lockRconnect) {
        return;
      }
      this.lockRconnect = true;
      // clearTimeout() 方法可取消由 setTimeout() 方法设置的定时操作。
      // clearTimeout() 方法的参数必须是由 setTimeout() 返回的 ID 值。
      // 注意: 要使用 clearTimeout() 方法, 在创建执行定时操作时要使用全局变量：
      // 没有连接上会一直重新连接，设置延迟避免请求过多,清除上一次的连接状态，5秒后重新连接
      this.timeoutnum && clearTimeout(this.timeoutnum);
      this.timeoutnum = setTimeout(() => {
        // 重新的新连接
        this.initWebSocket(params);
        console.log("10:%s：开启新的连接", params);
        this.lockRconnect = false;
      }, 5000);
    },
    // 心跳重置
    reset(params) {
      // 清除时间
      clearTimeout(this.timeoutObj);
      clearTimeout(this.serverTimeoutObj);
      // 重启心跳
      // console.log("1.%s:数据接收后重启心跳", params);
      this.start(params);
    },
    // 心跳开启
    start(params) {
      this.timeoutObj && clearTimeout(this.timeoutObj);
      this.serverTimeoutObj && clearTimeout(this.serverTimeoutObj);
      this.timeoutObj = setTimeout(() => {
        // 发送一个心跳，后端接收到后，返回一个心跳消息
        if (this.websock.readyState == 1) {
          // 连接正常，发送一个消息
          console.log("2.%s连接正常，发送一个消息", params);
          this.websock.send("heartCheck");
        } else {
          // 重新连接
          console.log("2.%s连接不正常，重新连接", params);
          this.reconnect(params);
        }
        this.serverTimeoutObj = setTimeout(() => {
          console.log("3.%s连接超时关闭socket连接", params);
          this.websock.close();
          // 即使连接上，页面也没有数据展示。连接超时需要刷新
          //刷新整个页面
          window.location.reload();
          //刷新当前页面
          // this.$router.go(0);
        }, this.timeout);
      }, this.timeout);
    }
  }
};
</script>

// scoped属性时表明style里的css样式只适用于当前组件元素
<style scoped lang='less'>
/* 导航栏切换 */
.nav-title {
  height: 60px;
  padding-top: 10px;
  border: 0 0 2px 0 solid #a1a1a1;
}
/* 地图的窗口高度设置 */
.nav-map {
  padding-top: 0px;
  margin-bottom: 2rem;
  background: white;
}
/* 设置高度和背景色 */
.dh-1 /deep/ .el-tabs__header {
  margin: 0px;
  height: 60px;
  background: white;
}
/* 修改切换栏的 字体大小 */
.dh-1 /deep/ .el-tabs__item {
  /* padding: 0, 8px; */
  font-size: 16px;
}
/* 修改切换栏 选中后的字体颜色和加粗展示 */
.dh-1 /deep/ .el-tabs__item.is-active {
  color: black;
  font-weight: bold;
}
/* 修改鼠标触碰展示的颜色 */
.dh-1 /deep/ .el-tabs__item:hover {
  color: #000;
}
.HiddenDisplay {
  width: 100px;
  word-break: keep-all;
  overflow: hidden;
  white-space: nowrap; 
  display: inline-block;
  text-overflow: ellipsis;
}
.HiddenDisplay:hover {
  overflow: visible;
}
/* dh-2右侧标题样式 注释同左侧 */
.dh-2 /deep/ .el-tabs__header {
  margin: 0px;
  height: 60px;
  background: white;
}
.dh-2 /deep/ .el-tabs__item {
  font-size: 16px;
}
.dh-2 /deep/ .el-tabs__item.is-active {
  color: black;
  font-weight: bold;
}
.dh-2 /deep/ .el-tabs__item:hover {
  color: #000;
}
/* 修改选择下划线的颜色，达到隐藏效果 */
.dh-2 /deep/ .el-tabs__active-bar {
  background-color: rgb(237, 237, 237);
}
</style>


