// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
// -----------------------------------------------

// 谷歌地图的样式引入
// import 'vue-googlemaps/dist/vue-googlemaps.css'
// import VueGoogleMaps from 'vue-googlemaps'
// Vue.use(VueGoogleMaps, {
//   load: {
//     //填入申请的apiKey账号
//     apiKey: 'AIzaSyCWlaWgMUBgWZPIRpwFNWbC9DYiA0w7CMw',
//     libraries: ['places'],
//     useBetaRenderer: false,
//   },
// })

// import echarts from 'echarts'
// //vue全局注册echarts
// Vue.prototype.$echarts = echarts

// 使用svg图标  必须在src下创建svg 
import Icon from 'vue-svg-icon/Icon.vue';
Vue.component('icon', Icon);

// 使用bootstrap
import 'jquery/dist/jquery.js'
import 'bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap/dist/js/bootstrap.min.js'

//使用elementUI
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import locale from 'element-ui/lib/locale/lang/en'
Vue.use(ElementUI, { locale })

import Vuex from 'vuex'
import store from "./store/store"
Vue.use(Vuex)

import Axios from 'axios'
Axios.defaults.withCredentials = true;//让ajax携带cookie
Vue.prototype.$axios = Axios

//中英文切换
import i18n from './i18n/i18n'
Vue.use(i18n)

// 自定义全局组件
import Global_com from './components/Global_components'
import Global_bar from './components/Global_information_bar'
import Global_echarts from './components/Global_echarts'
import Mshow from './components/Mshow'
Vue.component("glo", Global_com)
Vue.component("globar", Global_bar)
Vue.component("gloecharts", Global_echarts)
Vue.component("globalmap", Mshow)
// -----------------------------------------------
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  i18n,
  components: { App },
  template: '<App/>',

})



