<template>
  <!-- 导航条 -->
  <nav class="navbar navbar-default attom_nav">
    <!-- 改变导航栏的样式 -->
    <div v-bind:style="{'background-color':'#2F5597'}">
      <!-- 移动端特有的展示效果 -->
      <div class="navbar-header">
        <!-- navbar-toggle collapsed    collapsed（手风琴）：在移动端会有手风琴效果 -->
        <!-- data-toggle="collapse" 手风琴组件 -->
        <!-- data-target  说明控制哪一个导航条的   data开头的都是自定义属性    -->
        <button
          type="button"
          class="navbar-toggle collapsed"
          data-toggle="collapse"
          data-target="#bs-example-navbar-collapse-1"
          aria-expanded="false"
        >
          <!-- sr-only 屏幕阅读器 -->
          <span class="sr-only">Toggle navigation</span>
          <span class="icon-bar"></span>
          <span class="icon-bar"></span>
          <span class="icon-bar"></span>
        </button>
        <a class="navbar-brand motta_logo" href="#" v-bind:style="{'padding-top':'10px'}">
          <img src="../assets/20191011205450.png" class="img-responsive center-block" />
        </a>
      </div>
      <!-- 遍历导航栏-->
      <div class="collapse navbar-collapse" id="bs-example-navbar-collapse-1">
        <ul class="nav navbar-nav" ref="li_id">
          <li
            v-for="(item, index) in nav_list"
            :key="index"
            v-bind:class="{active:index == isActive}"
            @click="clickClass(index)"
          >
            <router-link
              :to="{name:item}"
              :index="isActive"
              v-bind:style="{'color':'white','padding-bottom': '1.7rem', 'font-size':'18px'}"
            >{{ item }}</router-link>
          </li>
        </ul>
        <!-- ---------------------个人中心------------------------------- -->
        <ul class="nav navbar-nav navbar-right">
          <li class="dropdown">
            <router-link
              :to="{name:'Configuration'}"
              v-bind:style="{color:'white', 'font-size':'18px'}"
            >Configuration</router-link>
          </li>
          <li>
            <a v-bind:style="{color:'white', 'font-size':'18px'}">|</a>
          </li>
          <li>
            <div class="head-portrait">
              <!-- <img src="../assets/logo.png" /> -->
            </div>
          </li>
          <li class="dropdown">
            <a
              href="#"
              class="dropdown-toggle"
              data-toggle="dropdown"
              role="button"
              aria-haspopup="true"
              aria-expanded="false"
              v-bind:style="{color:'white', 'font-size':'18px'}"
            >
              ChenJun
              <span class="glyphicon glyphicon-triangle-bottom"></span>
            </a>
            <ul class="dropdown-menu">
              <li>
                <a href="#">{{$t('nav.My_message')}}</a>
              </li>
              <li>
                <a href="#">{{$t('nav.Setting')}}</a>
              </li>
              <li role="separator" class="divider"></li>
              <li>
                <a @click="out()">{{$t('nav.drop_out')}}</a>
              </li>
            </ul>
          </li>
        </ul>
      </div>
    </div>
  </nav>
</template>

<script>
import { out_put } from "../Api/api";
export default {
  data() {
    return {
      isActive: 0, // 0为默认选择第一个，-1为不选择
      // 导航栏标签列表
      nav_list: ["Overview", "Site Detail", "History"],
      params:{}
    };
  },
  created() {
    this.params = {
        user: localStorage.getItem("user"),
        u_flg: localStorage.getItem("u_flg")
      };
  },
  mounted() {},
  methods: {
    clickClass(index) {
      this.isActive = index;
    },
    out() {
      out_put(this.params).then(e => {
        if (e.data == 'ok') {
          this.$router.push({name:'Login'});
        }
      });
    }
  }
};
</script>

<style scoped lang="less">
#app {
  margin-top: 8px;
}
.navbar {
  margin-bottom: 0px;
}
.attom_nav /deep/ .navbar-nav > .active > a {
  background-color: transparent;
}
.attom_nav /deep/ .navbar-default {
  background-color: transparent;
}
/* 导航栏整体颜色 */
.nva-style {
  color: "white";
  font-size: 18px;
}
/* 个人头像 */
.head-portrait {
  text-align: center;
  border: 2px solid #a1a1aa;
  background: #dddddd;
  margin-top: 7px;
  height: 40px;
  width: 40px;
  border-radius: 50%;
  overflow: hidden;
}
/* 给导航标签加选中的加深颜色 */
.active {
  background: #000980;
}
</style>


